
import { Component, OnInit, AfterViewInit } from '@angular/core';
import { IAlbum, IEvent, Lightbox, LIGHTBOX_EVENT, LightboxConfig, LightboxEvent } from 'ngx-lightbox';
import { Subscription } from 'rxjs';
import { DomSanitizer } from '@angular/platform-browser';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';

import { HttpService } from 'src/app/demo/Services/http/http.service';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import { IOption } from 'ng-select';
import { GovernorateReDto } from 'src/app/demo/_model/governorate';
import { Nationality } from 'src/app/demo/_model/nationality';
import { FormBuilder, FormGroup } from '@angular/forms';
import { TimeRe } from 'src/app/demo/_model/timeRe';

import 'sweetalert2/src/sweetalert2.scss';
import Swal from 'sweetalert2/dist/sweetalert2.js';
import { StudentAdd, StudentRe } from 'src/app/demo/_model/_Student/student';
import { StudentReportService } from 'src/app/demo/Services/studentReport/studentReport.service';
import { StudentReportRe } from 'src/app/demo/_model/StudentReport';


@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.scss']
})
export class UserProfileComponent implements OnInit, AfterViewInit {
  public activeTab: string;

  public editProfile: boolean;
  public editProfileIcon: string;

  public editContact: boolean;
  public editContactIcon: string;

  public editOtherInfo: boolean;
  public editOtherInfoIcon: string;

  public albums: Array<IAlbum>;
  private subscription: Subscription;
  StudentService: BaseService<StudentAdd>;
  student: StudentRe;
  studentReports: StudentReportRe[];
  studentReport: StudentReportRe;
  Nationalities: Array<IOption>;
  Governorates: Array<IOption>;
  Times: Array<IOption>;
  NationalityService: BaseService<Nationality>;
  GovernorateService: BaseService<GovernorateReDto>;
  timesService: BaseService<TimeRe>;
  form: FormGroup;
  timeArray = [];
  constructor(
    private lightbox: Lightbox,
    private lightboxEvent: LightboxEvent,
    private lighboxConfig: LightboxConfig,
    private sanitizer: DomSanitizer,
    private httpSv: HttpService,
    private accountService: AccountService,
    private studentReportSV: StudentReportService,
    private AccountService: AccountControllerService,
    private formBuilder: FormBuilder,

  ) {
    this.timesService = new BaseService<TimeRe>(httpSv, ApiPath.Time);
    this.NationalityService = new BaseService<Nationality>(httpSv, ApiPath.Nationality);
    this.GovernorateService = new BaseService<GovernorateReDto>(httpSv, ApiPath.Governorate);
    this.StudentService = new BaseService<StudentAdd>(httpSv, ApiPath.Student);
    this.activeTab = 'profile';

    this.editProfile = false;
    this.editProfileIcon = 'icon-edit';

    this.editContact = false;
    this.editContactIcon = 'icon-edit';

    this.editOtherInfo = false;
    this.editOtherInfoIcon = 'icon-edit';

    this.albums = [];
    for (let i = 1; i <= 6; i++) {
      const album = {
        src: 'assets/images/light-box/l' + i + '.jpg',
        caption: 'Image ' + i + ' caption here',
        thumb: 'assets/images/light-box/sl' + i + '.jpg'
      };

      this.albums.push(album);
    }
    lighboxConfig.fadeDuration = 1;
  }
  ngAfterViewInit(): void {

  }

  ngOnInit() {
    this.getData();
    this.initForm();
    this.GetReports();
  }

  GetReports() {
    this.studentReportSV.GetByUserId().subscribe(res => {
      this.studentReports = res;
      console.log(this.studentReports);

    })
  }
  getReportDetails(report: any, modal: any) {
    this.studentReport = report;
    modal.show();
  }
  getData() {
    this.accountService.getCurrentUserWithoutRouting();
    this.StudentService.GetById(this.accountService.user.id).subscribe(res => {
      this.student = res;
      console.log(this.student);
      console.log(res);
    });
    this.NationalityService.GetAll().subscribe((res: Nationality[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.name,
          value: x.id.toString()
        };
        return temp;
      });

      this.Nationalities = data;
      console.log(this.Nationalities);
    });
    this.GovernorateService.GetAll().subscribe((res: GovernorateReDto[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.name_ar,
          value: x.id.toString()
        };
        return temp;
      });
      this.Governorates = data;
      console.log(this.Governorates);
    });

    this.timesService.GetAll().subscribe((res: TimeRe[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.dayName,
          value: x.id.toString()
        };
        return temp;
      });
      this.Times = data;
      console.log(this.Times);
    });
  }




  open(index: number): void {
    this.subscription = this.lightboxEvent.lightboxEvent$.subscribe((event: IEvent) => this._onReceivedEvent(event));
    this.lightbox.open(this.albums, index, { wrapAround: true, showImageNumberLabel: true });
  }

  private _onReceivedEvent(event: IEvent): void {
    if (event.id === LIGHTBOX_EVENT.CLOSE) {
      this.subscription.unsubscribe();
    }
  }

  initForm(data: StudentRe = this.student) {

    this.form = this.formBuilder.group({
      id: [data?.id | 0],
      firstName: [data?.firstName],
      lastName: [data?.lastName],
      email: [data?.email],
      address: [data?.address],
      age: [data?.age],
      mobileNumber: [data?.mobileNumber],
      nationalityId: [data?.nationality?.id.toString()],
      governorateId: [data?.governorate?.id.toString()],
      savedParts: [data?.savedParts],
    });

  }
  Update(): void {
    let data: StudentAdd = this.form.value;
    this.StudentService.Update(data).subscribe((res) => {
      this.student = res;
      Swal.fire('تم', 'تم تحديث بياناتك بنجاح', 'success');
    });
  }
}
